import classNames from 'classnames'
import { Popup, Toast } from 'antd-mobile'
import { Outlet, NavLink } from 'react-router-dom'
import { useCallback, useContext, useState } from 'react'

import styles from './style.module.scss'
import { menus } from '../../libs/menus'
import { context } from '../../hooks/store'
import * as userService from '../../services/user'
import { ReactComponent as IconMenu } from '../../assets/imgs/menu.svg'

export default function Home() {
  const [menuVisible, setMenuVisible] = useState<boolean>(false)
  const { user, setUser } = useContext(context)

  const logOut = useCallback(async () => {
    try {
      setUser({
        nickname: '',
        account: '',
      })
      const res = await userService.logOut()
      if (res.code === 0) {
        Toast.show('')
      } else {
        Toast.show(res.message)
      }
    } catch (error) {}
  }, [setUser])

  return (
    <div className={styles.home}>
      <div className={styles.header}>
        <button className={styles.menuBtn} onClick={() => setMenuVisible(true)}>
          <IconMenu />
        </button>
      </div>
      <main className={styles.main}>{<Outlet />}</main>
      <Popup
        visible={menuVisible}
        onMaskClick={() => {
          setMenuVisible(false)
        }}
        position='left'
        bodyStyle={{ width: '240px' }}
      >
        <div className={styles.menu}>
          <div className={styles.menuHeader}>
            <button className={styles.menuBtn} onClick={() => setMenuVisible(false)}>
              <IconMenu />
            </button>
            <span className={styles.nickName}>{user.nickname}</span>
          </div>
          <nav className={styles.menuContent}>
            {menus.map((tab, i) => (
              <NavLink
                to={tab.link}
                key={i}
                onClick={() => {
                  setMenuVisible(false)
                  tab.link === '/login' && logOut()
                }}
                className={({ isActive }) =>
                  classNames(styles.menuItem, { [styles.active]: isActive })
                }
              >
                <tab.icon />
                <span>{tab.name}</span>
              </NavLink>
            ))}
          </nav>
        </div>
      </Popup>
    </div>
  )
}
