import { useCallback, useEffect, useMemo, useState } from 'react'

import { ITodo, TodoType } from '../libs/models'
import * as todoService from '../services/todo'

export default function useTodos(type: TodoType) {
  const [todos, setTodos] = useState<ITodo[]>([])
  const [loading, setLoading] = useState<boolean>(false)
  const [skip, setSkip] = useState<number>(0)
  const [total, setTotal] = useState<number>(0)
  const [error, setError] = useState<string>('')

  const hasMore = useMemo(() => {
    return skip < total
  }, [skip, total])

  const listTodos = useCallback(async () => {
    try {
      setLoading(true)
      setError('')
      const { code, message, data } = await todoService.list(type)
      if (code === 0) {
        setTodos(data.items)
        setSkip(data.items.length)
        setTotal(data.total)
      } else {
        setError(message)
      }
    } catch (error: any) {
      setError(error.message)
    } finally {
      setLoading(false)
    }
  }, [type])

  const loadMore = useCallback(async () => {
    try {
      setLoading(true)
      setError('')
      const { code, message, data } = await todoService.list(type, skip)
      if (code === 0) {
        setTodos(todos.concat(data.items))
        setSkip(skip + data.items.length)
        setTotal(data.total)
      } else {
        setError(message)
      }
    } catch (error: any) {
      setError(error.message)
      throw error
    } finally {
      setLoading(false)
    }
  }, [skip, todos, type])

  const addTodo = useCallback(
    async (content: string) => {
      try {
        const { code } = await todoService.create(content)
        if (code === 0) await listTodos()
      } catch (error: any) {
        throw error
      }
    },
    [listTodos]
  )

  const updateTodo = useCallback(
    async (todoId: string, type: string) => {
      try {
        const { code } = await todoService.update(todoId, type)
        if (code === 0) await listTodos()
      } catch (error) {
        throw error
      }
    },
    [listTodos]
  )

  const removeTodo = useCallback(
    async (todoId: string) => {
      try {
        const { code } = await todoService.remove(todoId)
        if (code === 0) await listTodos()
      } catch (error) {
        throw error
      }
    },
    [listTodos]
  )

  useEffect(() => {
    listTodos()
  }, [listTodos])

  return {
    todos,
    hasMore,
    loading,
    error,
    listTodos,
    loadMore,
    addTodo,
    updateTodo,
    removeTodo,
  }
}
