import { useState, useCallback, useEffect } from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom'

import Home from '../Home'
import SignIn from '../SignIn'
import ChangePwd from '../../components/ChangePwd'
import TodoList from '../../components/TodoList'
import { IUser, TodoType } from '../../libs/models'
import { StoreProvider } from '../../hooks/store'
import * as userService from '../../services/user'

export default function App() {
  const [user, setUser] = useState<IUser>({} as IUser)

  const getInfo = useCallback(async () => {
    const res = await userService.getInfo()
    if (res.data) setUser(res.data)
  }, [])

  useEffect(() => {
    getInfo()
  }, [getInfo])

  return (
    <StoreProvider value={{ user, setUser }}>
      <BrowserRouter>
        <Routes>
          <Route path='/' element={<Home />}>
            <Route path='' element={<TodoList type={TodoType.Task} />} />
            <Route path='important' element={<TodoList type={TodoType.Important} />} />
            <Route path='finished' element={<TodoList type={TodoType.Finished} />} />
            <Route path='changePwd' element={<ChangePwd />} />
          </Route>
          <Route path='/signup' element={<SignIn isRegistered={false} />} />
          <Route path='/login' element={<SignIn isRegistered={true} />} />
        </Routes>
      </BrowserRouter>
    </StoreProvider>
  )
}
