import classNames from 'classnames'
import { useCallback, useEffect, useState } from 'react'
import { InfiniteScroll, Popup, PullToRefresh, Toast } from 'antd-mobile'

import TaskItem from './TodoItem'
import styles from './style.module.scss'
import useTodos from '../../hooks/useTodos'
import { TodoType } from '../../libs/models'
import { ReactComponent as IconPlus } from '../../assets/imgs/plus.svg'
import { ReactComponent as IconEmpty } from '../../assets/imgs/empty.svg'
import { ReactComponent as IconChecked } from '../../assets/imgs/checked.svg'
import { ReactComponent as IconHomeFill } from '../../assets/imgs/home_fill.svg'
import { ReactComponent as IconStarFill } from '../../assets/imgs/star_fill.svg'

interface TodoListProps {
  type: TodoType
}

export default function TodoList({ type }: TodoListProps) {
  const [opVisible, setOpVisible] = useState<boolean>(false)
  const [currentId, setCurrentId] = useState<string>('')
  const [input, setInput] = useState<string>('')
  const { todos, error, hasMore, listTodos, loadMore, addTodo, updateTodo, removeTodo } =
    useTodos(type)

  useEffect(() => {
    error && Toast.show('')
  }, [error])

  const openPopup = useCallback((id: string) => {
    setCurrentId(id)
    setOpVisible(true)
  }, [])

  const addTaskHandle = useCallback(
    async (content: string | undefined) => {
      if (content === undefined || content === '') return
      addTodo(content)
      setInput('')
    },
    [addTodo]
  )

  return (
    <>
      <div className={styles.title}>
        {type === 'task' && (
          <>
            <IconHomeFill />
            <span></span>
          </>
        )}
        {type === 'important' && (
          <>
            <IconStarFill />
            <span></span>
          </>
        )}
        {type === 'finished' && (
          <>
            <IconChecked />
            <span></span>
          </>
        )}
      </div>
      <div
        className={
          todos.length === 0
            ? classNames(styles.todolist, styles.listEmpty)
            : styles.todolist
        }
      >
        {todos.length === 0 ? (
          <IconEmpty />
        ) : (
          <PullToRefresh onRefresh={listTodos}>
            {todos.map((item) => (
              <TaskItem
                key={item._id}
                task={item}
                setImportant={() => updateTodo(item._id, 'important')}
                setFinished={() => updateTodo(item._id, 'finished')}
                removeTodo={() => removeTodo(item._id)}
                onClick={() => openPopup(item._id)}
              />
            ))}
            <InfiniteScroll hasMore={hasMore} loadMore={loadMore} />
          </PullToRefresh>
        )}
      </div>
      {type === 'task' && (
        <div className={styles.taskInput}>
          <IconPlus />
          <input
            type='text'
            value={input}
            maxLength={200}
            onChange={(e) => setInput(e.target.value)}
            placeholder=''
            onKeyDown={(e) => {
              e.key === 'Enter' && addTaskHandle(input)
            }}
          />
        </div>
      )}
      <Popup
        visible={opVisible}
        onMaskClick={() => {
          setOpVisible(false)
        }}
        position='bottom'
      >
        <div className={styles.op}>
          <div className={styles.opHeader}></div>
          <div
            className={styles.opItem}
            onClick={() => {
              updateTodo(currentId, 'finished')
              setOpVisible(false)
              setCurrentId('')
            }}
          >
            {currentId === ''
              ? ''
              : todos.filter((todo) => todo._id === currentId)[0].finished
              ? ''
              : ''}
          </div>
          <div
            className={styles.opItem}
            onClick={() => {
              updateTodo(currentId, 'important')
              setOpVisible(false)
              setCurrentId('')
            }}
          >
            {currentId === ''
              ? ''
              : todos.filter((todo) => todo._id === currentId)[0].important
              ? ''
              : ''}
          </div>
          <div
            className={classNames(styles.opItem, styles.opItemRemove)}
            onClick={() => {
              removeTodo(currentId)
              setOpVisible(false)
              setCurrentId('')
            }}
          >
            
          </div>
          <div className={styles.space}></div>
          <div
            className={styles.opItem}
            onClick={() => {
              setOpVisible(false)
              setCurrentId('')
            }}
          >
            
          </div>
        </div>
      </Popup>
    </>
  )
}
