import request from '../libs/request'
import { ApiResp, ITodo, TodoType } from '../libs/models'

/**
 * 
 * @param todoType 
 * @param skip 
 * @param limit 
 */
export async function list(todoType: TodoType, skip?: number, limit?: number) {
  const query = {
    todoType: todoType.toString(),
    skip: skip || 0,
    limit: limit || 15
  }
  const { data } = await request.post<ApiResp<{ items: ITodo[]; total: number }>>(
    '/api/todo/list',
    query
  )
  return data
}

/**
 * 
 * @param content 
 */
export async function create(content: string) {
  const { data } = await request.post<ApiResp<{ todoId: string }>>('/api/todo/create', {
    content: content
  })
  return data
}

/**
 * 
 * @param todoId id
 * @param updateProp 
 */
export async function update(todoId: string, updateProp: string) {
  const { data } = await request.post<ApiResp>('/api/todo/update', {
    todoId: todoId,
    updateProp: updateProp
  })
  return data
}

/**
 * 
 * @param todoId id
 */
export async function remove(todoId: string) {
  const { data } = await request.post<ApiResp>('/api/todo/remove', {
    todoId: todoId
  })
  return data
}
