import { useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import { Form, Input, Toast } from 'antd-mobile'

import styles from './style.module.scss'
import { context } from '../../hooks/store'
import * as userService from '../../services/user'
import { ReactComponent as IconPwd } from '../../assets/imgs/password.svg'

interface ChangePwdValues {
  oldPwd: string
  newPwd: string
}

export default function ChangePwd() {
  const navigate = useNavigate()
  const { setUser } = useContext(context)
  const [form] = Form.useForm<ChangePwdValues>()

  const changePwd = async () => {
    try {
      const values = await form.validateFields()
      const res = await userService.changePwd(values.oldPwd, values.newPwd)
      if (res.code === 0) {
        Toast.show('')
        form.resetFields()
        setUser({
          nickname: '',
          account: '',
        })
        navigate('/login')
      } else {
        Toast.show(res.message)
      }
    } catch (error) {
      Toast.show('')
    }
  }

  return (
    <div className={styles.pwdPage}>
      <div className={styles.title}>
        <IconPwd />
        <span></span>
      </div>
      <div className={styles.formWrap}>
        <div className={styles.container}>
          <Form form={form}>
            <Form.Item
              name='oldPwd'
              rules={[
                {
                  required: true,
                  message:
                    '8~16 .?!@  Admin123.',
                },
              ]}
            >
              <Input placeholder='' type='password' minLength={8} maxLength={16} />
            </Form.Item>
            <Form.Item
              name='newPwd'
              rules={[
                {
                  required: true,
                  message:
                    '8~16 .?!@  Admin123.',
                },
              ]}
            >
              <Input placeholder='' type='password' minLength={8} maxLength={16} />
            </Form.Item>
            <Form.Item>
              <button onClick={changePwd}></button>
            </Form.Item>
          </Form>
        </div>
      </div>
    </div>
  )
}
