import { Form, Input, Toast } from 'antd-mobile'
import { useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

import styles from './style.module.scss'
import { context } from '../../hooks/store'
import * as userService from '../../services/user'
import { ReactComponent as Todo } from '../../assets/imgs/TODO.svg'

interface LogInProps {
  isRegistered: boolean
}

interface SignInValues {
  account: string
  nickname: string
  password: string
}

export default function SignIn({ isRegistered }: LogInProps) {
  const [form] = Form.useForm<SignInValues>()
  const { user, setUser } = useContext(context)
  const navigate = useNavigate()

  const signUp = async () => {
    try {
      const values = await form.validateFields()
      const res = await userService.signUp(
        values.account,
        values.nickname,
        values.password
      )
      if (res.code === 0) {
        Toast.show('')
        form.resetFields()
        navigate('/login')
      } else {
        Toast.show(res.message)
      }
    } catch (error) {
      Toast.show('')
    }
  }

  const login = async () => {
    try {
      const values = await form.validateFields()
      const res = await userService.logIn(values.account, values.password)
      if (res.code === 0) {
        const res = await userService.getInfo()
        res.data && setUser(res.data)
        form.resetFields()
        Toast.show('')
        navigate('/')
      } else {
        Toast.show(res.message)
      }
    } catch (error) {
      Toast.show('')
    }
  }

  useEffect(() => {
    user.nickname && navigate('/')
  }, [navigate, user])

  return (
    <div className={styles.loginPage}>
      <div className={styles.content}>
        <div className={styles.title}>
          <Todo />
          <span>To Do</span>
        </div>
        <div className={styles.container}>
          <Form form={form}>
            <Form.Item
              name='account'
              rules={[{ required: true, message: ' 11 ' }]}
            >
              <Input placeholder='Account' type='text' minLength={11} maxLength={11} />
            </Form.Item>
            {!isRegistered && (
              <Form.Item
                name='nickname'
                rules={[{ required: true, message: ' 1~7 ' }]}
              >
                <Input placeholder='Nickname' type='text' minLength={1} maxLength={7} />
              </Form.Item>
            )}
            <Form.Item
              name='password'
              rules={[
                {
                  required: true,
                  message:
                    '8~16 .?!@  Admin123.',
                },
              ]}
            >
              <Input
                placeholder='Password'
                type='password'
                minLength={8}
                maxLength={16}
              />
            </Form.Item>
            <Form.Item>
              {isRegistered ? (
                <button onClick={() => login()}>Log in</button>
              ) : (
                <button onClick={() => signUp()}>Sign up</button>
              )}
            </Form.Item>
          </Form>
          {isRegistered ? (
            <p className={styles.callout}>
              New to To-Do? <a href='/signup'> Create an account</a> .
            </p>
          ) : (
            <p className={styles.callout}>
              Already have an account? <a href='/login'> Log in</a> .
            </p>
          )}
        </div>
      </div>
    </div>
  )
}
