import request from '../libs/request'
import { ApiResp } from '../libs/models'

/**
 * 
 * @param account 
 * @param nickname 
 * @param password 
 */
export async function signUp(account: string, nickname: string, password: string) {
  const { data } = await request.post<ApiResp>('/api/user/signup', {
    account: account,
    nickname: nickname,
    password: password
  })
  return data
}

/**
 * 
 * @param account 
 * @param password 
 */
export async function logIn(account: string, password: string) {
  const { data } = await request.post<ApiResp>('/api/user/login', {
    account: account,
    password: password
  })
  return data
}

/**
 * 
 */
export async function getInfo() {
  const { data } = await request.get<ApiResp>('/api/user/getInfo')
  return data
}

/**
 * 
 * @param oldPwd 
 * @param newPwd 
 */
export async function changePwd(oldPwd: string, newPwd: string) {
  const { data } = await request.post<ApiResp>('/api/user/changePwd', {
    oldPwd: oldPwd,
    newPwd: newPwd
  })
  return data
}

/**
 * 
 */
export async function logOut() {
  const { data } = await request.post<ApiResp>('/api/user/logout')
  return data
}
